@charset "UTF-8";

@import "import/lib-fonts";
@import "base";
@import "import/typography";
@import "import/reset-css";

/* clearfix */
.clearfix{
	*zoom:1;
	&:before, &:after{
		content:'';
		display:block;
	}
	&:after{
		clear:both;
	}
}

/* hidden */
.hidden {
    display: none;
    overflow: hidden;
    border: 0;
    position: relative;
    z-index: -1;
    width: 1px;
    height: 1px;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
}

body.popView{
    overflow: hidden;
    height: 100vh;
}

input::placeholder, textarea::placeholder{
    font-size: inherit;
    color: #aaa;
}

.aggro{font-family: 'SB Aggro', sans-serif;}

.f-right{float: right;}
.f-left{float: left;}

.po-r{position: relative;}
.po-a{position: absolute;}

.ov-h{overflow: hidden;}

.ta-center{text-align: center !important;}
.ta-left{text-align: left !important;}

.line_01{overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}

.pc_only{display: block;}
.mo_only{display: none;}

//color
.c_white{color: #fff !important;}
.c_pink{color: #e814ff !important;}
.c_light_gray{color: #ddd !important;}
.c_gradient{position: relative; background: linear-gradient(-180deg, rgba(255,19,152,1) 0%, rgba(165,36,255,1) 57%, rgba(18,140,255,1) 100%); -webkit-background-clip: text; -webkit-text-fill-color: transparent;}
.c_gradient.line{color: transparent !important; white-space: nowrap;}
.c_gradient.line::before{content:attr(data-text); position: absolute; left: 0; top: 0; width: 100%; height: 100%; font-size: inherit; -webkit-text-stroke-width: 6px; -webkit-text-stroke-color: white; white-space:nowrap;}
.c_gradient.line::after{content:attr(data-text); z-index: 1; position: absolute; left: 0; top: 0; width: 100%; height: 100%; background: linear-gradient(-180deg, rgba(255,19,152,1) 0%, rgba(165,36,255,1) 57%, rgba(18,140,255,1) 100%); -webkit-background-clip: text; -webkit-text-fill-color: transparent; white-space:nowrap;}

.c-purple{color: #8338ec !important;}

/* space */
$position: (t:top, b:bottom, r:right, l: left);
$total: 10;

@each $abbr, $pos in $position {
  @for $i from 0 to $total {
    .m#{$abbr}-#{$i} {margin-#{$pos}: $i*10px !important;}
    .p#{$abbr}-#{$i} {padding-#{$pos}: $i*10px !important;}
  }
}

@media all and (max-width:768px){
  .pc_only{display: none;}
  .mo_only{display: block;}
  .c_gradient.line::before{-webkit-text-stroke-width: 0.78vw;}

    /* space */
    $position: (t:top, b:bottom, r:right, l: left);
    $total: 10;

    @each $abbr, $pos in $position {
    @for $i from 0 to $total {
        .m#{$abbr}-#{$i} {margin-#{$pos}: $i*1.3vw !important;}
        .p#{$abbr}-#{$i} {padding-#{$pos}: $i*1.3vw !important;}
    }
    }

}

html{scroll-behavior: smooth;}
